// == Import npm
import React, { useState } from 'react';
import { Route, Switch } from 'react-router-dom';
import { Divider } from 'semantic-ui-react';
import BurgerMenu from '../BurgerMenu/BurgerMenu';
import Footer from '../Footer/Footer';
import Boissons from '../Items/Boissons/Boissons';
import Cicchetteria from '../Items/Cicchetteria/Cicchetteria';
import Dolce from '../Items/Dolce/Dolce';
import Pizze from '../Items/Pizze/Pizze';
import Vinotheque from '../Items/Vinotheque/Vinotheque';
import Cocktails from '../Items/Cocktails/Cocktails';
import Main from '../Main/Main';
import Title from '../Title/Title';

// == Import
import './app.scss';
import Cuisine from '../Items/Cuisine/Cuisine';

// == Composant
const App = () => {
  const [openMenu, setOpenMenu] = useState(false);
  return (
    <div className="app">
      <BurgerMenu openMenu={openMenu} setOpenMenu={setOpenMenu}>
        <Title setOpenMenu={setOpenMenu} openMenu={openMenu} />
        <Divider />
        <Switch>
          <Route exact path="/">
            <Main />
          </Route>
          <Route path="/pizze">
            <Pizze />
          </Route>
          <Route path="/cicchetteria">
            <Cicchetteria />
          </Route>
          <Route path="/cuisine">
            <Cuisine />
          </Route>
          <Route path="/boissons">
            <Boissons />
          </Route>
          <Route path="/vins">
            <Vinotheque />
          </Route>
          <Route path="/dolce">
            <Dolce />
          </Route>
          <Route path="/cocktails">
            <Cocktails />
          </Route>
        </Switch>
        <Divider />
        <Footer />
      </BurgerMenu>
    </div>
  );
};

// == Export
export default App;
